.content {
  flex: 1 0 auto;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.router-content {
  padding: 24px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 24px;
}

@media only screen and (max-width: 1000px) {
  .wrapper {
    padding: 12px;
  }

  .router-content {
    padding: 24px 12px;
  }
}
